.calender {
  width: 1200px;
  height: 800px;
  margin: 4% auto 80px;
}
.calender img {
  width: 1200px;
  height: 800px;
}
.infoWrap {
  width: 1200px;
  display: flex;
  padding-top: 3%;
  margin-left: auto;
  margin-right: auto;
  border-top: 1px solid #d8d8d8;
  align-items: center;
}

.roomInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 5%;
}
.roomInfo .info {
  color: #474747;
  line-height: 25px;
  margin-bottom: 4%;
}
.roomInfo span {
  display: inline;
  color: #179bd7;
  font-family: "NanumSquareNeoBold";
}

.roomInfo button {
  background-color: #179bd7;
  border: none;
  color: white;
  padding: 5px 3px;
  width: 170px;
  font-size: 16px;
  border-radius: 15px;
  margin-bottom: 4%;
}

.title {
  font-family: "NanumSquareNeoBold";
  font-size: 25px;
  margin-bottom: 4%;
}

.locationInfo {
  font-family: "NanumSquareNeoBold";
  font-size: 20px;
}
.locationWrap {
  margin-bottom: 10px;
}
.locationDetail {
  font-size: 16px;
  margin-bottom: 4%;
}
.warning {
  color: rgb(255, 77, 77);
  font-size: 14px;
  line-height: 25px;
}

@media (max-width: 1750px) {
  .roomInfo {
    margin-left: 20px;
  }
  .infoWrap {
    width: 900px;
  }
  .infoWrap > img {
    width: 450px;
    height: 280px;
  }
  .title {
    font-size: 22px;
  }
  .locationWrap {
    display: flex;
    align-items: center;
  }
  .locationWrap > p {
    margin-left: 10px;
    font-size: 16px;
  }

  .calender {
    width: 900px;
    height: 600px;
  }
  .calender img {
    width: 900px;
    height: 600px;
  }
}
