.card {
  box-sizing: border-box;
  border: 1px solid #d8d8d8;
  width: 490px;
  margin-bottom: 20px;
  padding: 13px;
  flex-direction: column;
}

.card .profile .profilePic {
  width: 70px;
  height: 105px;
  margin-right: 10px;
  background-color: #f4f4f4;
}
.card .profile {
  display: flex;
}
.card .duty {
  background-color: #f4f4f4;
  padding: 15px;
  margin-top: 20px;
  font-size: 15px;
  height: 128px;
  overflow: scroll;
}
.CardInfoTeam {
  font-size: 18px;
}

.subInfoWrap p {
  display: inline;
  font-size: 15px;
}
.subInfoWrap div {
  display: inline-flex;
}

.subInfoWrap {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.subInfoWrap em {
  border-left: 2px solid #179bd7;
  margin-left: 10px;
}
.subInfoWrap .content {
  padding-left: 10px;
}
.underWrap {
  margin-top: 20px;
}
.title:nth-of-type(odd) {
  width: 110px;
}

@media (max-width: 1750px) {
  .card {
    width: 420px;
  }
  .subInfoWrap p {
    display: block;
    padding: 4px 0;
  }
  .subInfoWrap div {
    display: block;
  }
  .subInfoWrap {
    margin-top: 10px;
  }
  .underWrap {
    margin-top: 0;
  }
  .title .titleMargin {
    margin-right: 14px;
  }
}
