.infoWrap {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.title {
  border-bottom: 1px solid #d8d8d8;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-size: 18px;
  line-height: 28px;
  padding-bottom: 24px;
}

.infoWrap .infoTitle {
  font-size: 26px;
  color: #01348b;
  margin-bottom: 30px;
  margin-top: 60px;
}

.infoWrap li {
  line-height: 35px;
  font-size: 18px;
}

.infoWrap li::marker {
  color: #179bd7;
}

.periodWrap {
  margin-top: 100px;
}

.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
  border: none;
  border-bottom: 1px solid #d8d8d8;
}

.tableWrap {
  border-top: #01348b 2px solid;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}

.graphWidth li:nth-child(1) {
  width: 7%;
}

.graphWidth li:nth-child(2) {
  width: 50%;
}

.graphWidth li:nth-child(3) {
  width: 15%;
}

.graphWidth li:nth-child(4) {
  width: 20%;
}

.graphWidth li:nth-child(5) {
  width: 15%;
}

.subtitle {
  font-size: 30px;
  font-family: "NanumSquareNeoBold";
  text-align: center;
  margin: 100px 0 30px;
}

.btnWrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 1200px;
  border-top: #d8d8d8 1px solid;
  border-bottom: #d8d8d8 1px solid;
  padding: 20px 40px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  margin-bottom: 20px;
  font-size: 20px;
}

.btnWrap p:hover {
  color: #01348b;
  font-weight: 600;
}

.btnWrap p {
  cursor: pointer;
}

.secondWrap,
.forthWrap {
  width: 897px;
  display: flex;
  justify-content: space-between;
  margin-left: 50px;
  margin-bottom: 40px;
}

.thirdWrap,
.firstWrap {
  width: 890px;
  margin-left: 260px;
  display: flex;
  justify-content: space-between;
}

.totalWrap {
  position: relative;
}

.periodWrap {
  position: absolute;
  top: -65px;
}

.firstWrap {
  margin-left: 230px;
  width: 900px;
}

.forthWrap {
  width: 970px;
  margin-left: 20px;
}

.devImg {
  margin-top: 50px;
}

.numWrap .num {
  font-size: 24px;
  color: #179bd7;
  font-family: "NanumSquareNeoBold";
  padding-bottom: 10px;
}

@media (max-width: 1750px) {
  .infoWrap {
    width: 900px;
  }
  .title {
    width: 900px;
    word-break: keep-all;
  }
  .title > p {
    width: 750px;
    margin: 0 auto;
    font-size: 17px;
  }
  .infoWrap .infoTitle {
    font-size: 24px;
  }
  .infoWrap li {
    line-height: 30px;
    font-size: 18px;
    list-style-position: inside;
    text-indent: -23px;
    margin-left: 23px;
    word-break: keep-all;
  }

  .devImg {
    width: 900px;
    margin-top: 20px;
  }
  .periodWrap {
    top: -100px;
  }
  .secondWrap {
    width: 688px;
    margin-left: 30px;
    margin-bottom: 0px;
    margin-top: -30px;
  }
  .firstWrap {
    width: 800px;
    margin: 0 0 0 160px;
  }
  .thirdWrap {
    width: 688px;
    margin: 15px 0 0 184px;
  }
  .forthWrap {
    width: 800px;
    margin: 0 0 0 0px;
  }
  .numWrap {
    margin-bottom: 20px;
    width: 160px;
  }
  .numWrap .num {
    font-size: 20px;
  }

  .tableWrap {
    width: 900px;
  }
  .subtitle {
    font-size: 26px;
  }
  .btnWrap {
    width: 900px;
    font-size: 17px;
  }
}
