.greetingWrap {
  padding-top: 60px;
  display: flex;

  border-top: 1px solid #d8d8d8;
}

.greetingWrap .title {
  font-size: 28px;
  line-height: 40px;
  color: #01348b;
  font-family: "NanumSquareNeoBold";
}
.greetingWrap .content {
  font-size: 16px;
  line-height: 28px;
  font-family: "NanumSquareNeoRegular";
  margin-bottom: 80px;
  word-break: keep-all;
}
span {
  display: block;
}
.greetingWrap .name {
  font-family: "NanumSquareNeoLight";
  font-size: 22px;
}
.greetingWrap .schoolImg {
  width: 428px;
  height: 441px;
  margin-top: 5%;
  margin-left: 3%;
}
.greetingWrap .schoolImg02 {
  display: none;
}

.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.container .sign {
  bottom: 80px;
  left: 300px;
  position: absolute;
}

/* 반응형 */
@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .greetingWrap {
    position: relative;
    display: block;
  }
  .greetingWrap .schoolImg {
    display: none;
  }
  .greetingWrap .schoolImg02 {
    display: block;
  }
  .greetingWrap .title {
    font-size: 26px;
    position: absolute;
    top: 190px;
    padding: 30px 80px 30px 20px;
    background: #01348bcb;
    color: #ffffff;
  }
  .container .sign {
    bottom: 0;
  }
}
