.bannerContainer {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bannerImg {
  width: 100%;
  height: 713.625px;
  object-fit: cover;
}

.bannerTxt {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  color: #ffffff;
  text-shadow: 4px 4px 4px #424242;
  font-weight: 600;
}

.bannerTxt p {
  font-size: 34px;
  margin-bottom: 30px;
  margin-top: -50px;
}

.bannerTxt h2 {
  font-size: 52px;
}

.bannerBottom {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 160px;
  background: #002d6b73;
}

.bannerInner {
  width: 1200px;
  display: flex;
  justify-content: space-between;
}

.line {
  border-left: 2px solid #ffffff;
  height: 50px;
  margin: 0 10px;
}

.pageMove:nth-child(4) .line {
  display: none;
}

.pageMove {
  display: flex;
  color: #ffffff;
  align-items: center;
}

.pageMove img {
  margin-right: 15px;
  height: 48px;
}

.moveTxt a {
  color: #ffffff;
}

.moveTxt h3 {
  font-size: 18px;
  margin-bottom: 15px;
}

.moveTxt p {
  font-size: 14px;
  line-height: 22px;
  width: 200px;
  word-break: keep-all;
}

/* 반응형 */
@media (max-width: 1750px) {
  .bannerContainer {
    padding-top: 80px;
  }
  .bannerTxt {
    top: 30px;
  }
  .pageMove {
    display: block;
    text-align: center;
  }
  .pageMove img {
    margin: 0 0 10px 0;
    height: 38px;
  }
  .moveTxt h3 {
    font-size: 16px;
    margin-bottom: 10px;
  }
  .line {
    position: absolute;
    top: 50%;
    margin-left: 235px;
    transform: translate(-50%, -50%);
  }
}
