.container {
  width: 400px;
  margin: 50px auto 0;
}

.loginForm {
  text-align: center;
  width: 100%;
  height: 420px;
  border: 1px solid #b5b5b554;
  border-radius: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, 0.1);
}

.loginForm > img {
  margin: 30px 0;
}

.loginForm h2 {
  font-size: 20px;
  margin-bottom: 40px;
}

.inputForm > div {
  border: 1px solid #b5b5b5;
  width: 300px;
  margin: 8px auto;
  border-radius: 5px;
  box-sizing: border-box;
}

.loginBox {
  width: 260px;
  height: 36px;
  border: none;
  outline: none;
}

.loginIcon {
  color: #b5b5b5;
  font-size: 14px;
  margin-right: 10px;
  padding-top: 12px;
}

.loginBox::placeholder {
  font-family: "NanumSquareNeoLight";
}

.userInfo {
  width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 24px auto;
  font-size: 13px;
  cursor: pointer;
}

.joinLink p {
  color: #000000;
}

.loginButton {
  background: #01348b;
  border: none;
  width: 300px;
  height: 40px;
  color: #ffffff;
  font-family: "NanumSquareNeoRegular";
  font-size: 16px;
  margin: 20px 0;
  cursor: pointer;
}
