.subContainer {
  display: flex;
  margin-top: 60px;
}
.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 80px;
}
.teamList {
  display: none;
}
.teamWrap {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  border: 1px solid #d8d8d8;
  background-color: white;
  width: 158px;
  height: 444px;
  justify-content: center;
}
.eachTeam {
  border: none;
  background-color: white;
  padding: 15px 20px;
  text-align: left;
  font-size: 20px;
  cursor: pointer;
}
.focusTeam {
  background-color: #01348b;
  color: white;
}
.personWrap {
  width: 1050px;
}
.personWrap .title {
  font-size: 26px;
  margin-left: 50px;
  font-family: "NanumSquareNeoBold";
}
.cardWrap {
  width: 1000px;
  margin-top: 35px;
  display: flex;
  flex-wrap: wrap;
  margin-left: 50px;
  justify-content: space-between;
}

.search {
  text-align: center;
  margin: 40px 0px;
  width: 600px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

.search .searchBar {
  font-size: 20px;
  padding: 10px 0;
  border: none;
  width: 600px;
  border-bottom: 1.5px solid #d8d8d8;
  position: relative;
}

.searchIcon {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 5px;
  top: 15px;
}

@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .search {
    width: 500px;
  }
  .search .searchBar {
    width: 500px;
  }
  .teamList {
    display: block;
    display: flex;
  }
  .teamWrap {
    display: none;
  }
  .eachTeam {
    padding: 0 20px;
    text-align: center;
    font-size: 17px;
    cursor: pointer;
  }
  .eachTeam:hover {
    font-weight: 600;
    color: #01348b;
  }
  .cardWrap {
    width: 900px;
    margin-left: 0;
  }
  .personWrap .title {
    margin-left: 0;
  }
}
