.Container {
  display: flex;
  width: 1200px;
  text-align: center;
  flex-direction: column;
  margin: 0 auto;
}
.btnWrap button {
  width: 225px;
}
.title {
  font-size: 24px;
  margin-bottom: 56px;
  font-weight: 600;
}
.info {
  line-height: 27px;
  margin-bottom: 96px;
}

.info span {
  border-bottom: 2px solid #179bd7;
  padding: 1px;
  display: inline;
  font-family: "NanumSquareNeoBold";
}
.workshopWrap {
  background-color: #f0f1f2;
}
.workshopWrap .info {
  margin-bottom: 72px;
}
.workshopWrap .title {
  margin-top: 56px;
}
.workshopWrap img {
  margin-bottom: 80px;
}

.circle {
  width: 200px;
  height: 200px;
  border-radius: 100px;
  background-color: #ffffff;
  color: #179bd7;
  font-family: "NanumSquareNeoBold";
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  box-shadow: 3px 3px 5px 3px #a5a5a5;
  line-height: 28px;
  margin-left: auto;
  margin-right: auto;
}
.iconWrap {
  display: block;
}
.iconWrap .first {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  width: 360px;
  justify-content: space-between;
  margin-bottom: 32px;
}
.iconWrap .second {
  display: flex;
  width: 560px;
  margin-left: auto;
  margin-right: auto;
}
.iconWrap .third {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  width: 360px;
  margin-top: 32px;
  justify-content: space-between;
  margin-bottom: 80px;
}

@media (max-width: 1750px) {
  .Container {
    width: 900px;
  }
  .workshopWrap {
    width: 100vw;
    margin-left: calc(-50vw + 50%);
  }
}
