.subTitle {
  width: 900px;
  margin: 0 auto;
  text-align: center;
  word-break: keep-all;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 24px;
}

.containerWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.membership {
  display: flex;
  justify-content: space-between;
}

.membership p {
  display: flex;
  align-items: center;
  width: 280px;
  height: 46px;
  justify-content: center;
  border-radius: 10px;
  color: #ffffff;
  font-size: 20px;
}

.membership p:nth-of-type(1) {
  background-color: #179bd7;
}
.membership p:nth-of-type(2) {
  background-color: #1866d6;
}
.membership p:nth-of-type(3) {
  background-color: #1b55b2;
}
.membership p:nth-of-type(4) {
  background-color: #01348b;
}

.benefit {
  display: flex;
  flex-direction: column;
  font-size: 17px;
  justify-content: right;
}

.benefit p {
  margin-top: 20px;
  line-height: 26px;
  height: 70px;
  padding: 0 10px;
  text-align: center;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  word-break: keep-all;
}

.benefit p:nth-of-type(1) {
  background-color: #bbe2ef;
  width: 1180px;
}
.benefit p:nth-of-type(2) {
  background-color: #bbd8f9;
  width: 892px;
}
.benefit p:nth-of-type(3) {
  background-color: #8daddb;
  width: 586px;
}
.benefit p:nth-of-type(4) {
  background-color: #698bbc;
  width: 280px;
}

.joinStep {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.joinStep div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  margin: 0 50px;
}

.joinStep div > img {
  margin-bottom: 20px;
}

.joinStep div > p {
  font-size: 17px;
  color: #01348b;
  font-weight: 600;
}

.joinStepIcon {
  font-size: 24px;
  color: #bababa;
}

.situation {
  width: 100%;
  margin: 0 auto;
}

.situation h2 {
  display: flex;
  justify-content: center;
  font-size: 30px;
  color: #161616;
  margin: 80px 0 30px;
}

.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
}

.graphWidth li:nth-child(1) {
  width: 7%;
}
.graphWidth li:nth-child(2) {
  width: 48%;
}

.graphWidth li:nth-child(3),
.graphWidth li:nth-child(4),
.graphWidth li:nth-child(5) {
  width: 15%;
}

@media (max-width: 1750px) {
  .subTitle {
    width: 830px;
    font-size: 17px;
  }
  .containerWrap {
    width: 900px;
  }
  .membership p {
    width: 200px;
    height: 40px;
    font-size: 18px;
  }
  .benefit {
    font-size: 16px;
  }
  .benefit p:nth-of-type(1) {
    background-color: #bbe2ef;
    width: 880px;
  }
  .benefit p:nth-of-type(2) {
    background-color: #bbd8f9;
    width: 644px;
  }
  .benefit p:nth-of-type(3) {
    background-color: #8daddb;
    width: 412px;
  }
  .benefit p:nth-of-type(4) {
    background-color: #698bbc;
    width: 180px;
  }

  .joinStep div {
    width: 170px;
    height: 170px;
    margin: 0 20px;
  }
  .joinStep div > p {
    font-size: 16px;
  }

  .situation h2 {
    font-size: 26px;
  }
}
