.subTitle {
  width: 800px;
  margin: 0 auto;
  text-align: center;
  word-break: keep-all;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 24px;
}

.containerWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.containerWrap div .marginEdit {
  margin-bottom: 20px;
}

.liMarker {
  font-size: 20px;
}

.liMarker::marker {
  color: #179bd7;
}

.patentTable {
  border-collapse: collapse;
  width: 100%;
  border: 1px solid #d8d8d8;
}

.patentTh > tr > th {
  background-color: #e9e9e9;
}

.patentTh > tr > th,
.patentTb > tr > td {
  padding: 10px;
  border-spacing: 0;
  border: 1px solid #d8d8d8;
  text-align: center;
}

.procedure {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.proItem {
  display: flex;
  align-items: center;
  justify-content: center;
}

.procedure .proItem:nth-of-type(1),
.procedure .proItem:nth-of-type(2),
.procedure .proItem:nth-of-type(3),
.procedure .proItem:nth-of-type(4) {
  margin: 40px 40px 0;
}
.procedure .proItem:nth-of-type(5),
.procedure .proItem:nth-of-type(6),
.procedure .proItem:nth-of-type(7),
.procedure .proItem:nth-of-type(8),
.procedure .proItem:nth-of-type(9) {
  margin: 60px 30px 20px;
}

.proItem .proNum {
  width: 75px;
  height: 75px;
  background: linear-gradient(45deg, #01348b, #179bd7);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  border-radius: 50%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 140px 140px 0;
  font-size: 22px;
  font-weight: 600;
  color: #ffffff;
}

.proItem .proData {
  width: 180px;
  height: 180px;
  background: #ffffff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.proItem .proData img {
  margin-bottom: 20px;
}

.proItem .proData p {
  margin: 0 30px;
  text-align: center;
  word-break: keep-all;
  font-size: 14px;
}

.patentDot {
  position: absolute;
  margin-left: 260px;
}

.procedure .proItem:nth-of-type(9) .patentDot {
  display: none;
}

.procedure .proItem:nth-of-type(5) .patentDot,
.procedure .proItem:nth-of-type(6) .patentDot,
.procedure .proItem:nth-of-type(7) .patentDot,
.procedure .proItem:nth-of-type(8) .patentDot {
  margin-left: 240px;
}

.patentList h2 {
  display: flex;
  justify-content: center;
  font-size: 30px;
  color: #161616;
  margin: 80px 0 30px;
}

.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
}

.graphWidth li:nth-child(1) {
  width: 7%;
}
.graphWidth li:nth-child(2) {
  width: 48%;
}

.graphWidth li:nth-child(3),
.graphWidth li:nth-child(4),
.graphWidth li:nth-child(5) {
  width: 15%;
}

@media (max-width: 1750px) {
  .subTitle {
    font-size: 17px;
  }
  .liMarker {
    font-size: 18px;
    list-style-position: inside;
    text-indent: -23px;
    margin-left: 23px;
    word-break: keep-all;
  }

  .procedure .proItem:nth-of-type(1),
  .procedure .proItem:nth-of-type(2),
  .procedure .proItem:nth-of-type(3),
  .procedure .proItem:nth-of-type(4) {
    margin: 0;
  }
  .procedure .proItem:nth-of-type(5),
  .procedure .proItem:nth-of-type(6),
  .procedure .proItem:nth-of-type(7),
  .procedure .proItem:nth-of-type(8),
  .procedure .proItem:nth-of-type(9) {
    margin: 0;
  }
  .proItem {
    justify-content: space-between;
    width: 33.3%;
    flex-direction: column;
    margin: 30px 0 !important;
  }
  .proItem .proNum {
    width: 65px;
    height: 65px;
  }
  .proItem .proData {
    width: 170px;
    height: 170px;
  }
  .patentDot {
    position: absolute;
    margin: 85px 0 0 300px;
  }
  .procedure .proItem:nth-of-type(5) .patentDot,
  .procedure .proItem:nth-of-type(7) .patentDot,
  .procedure .proItem:nth-of-type(8) .patentDot {
    margin: 85px 0 0 300px;
  }
  .procedure .proItem:nth-of-type(3) .patentDot,
  .procedure .proItem:nth-of-type(6) .patentDot {
    display: none;
  }

  .patentList h2 {
    font-size: 26px;
  }
}
