.map {
  width: 1200px;
  height: 600px;
  background-color: rgb(229, 234, 184);
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}

.map img {
  width: 1200px;
  height: 600px;
}
.mapInfo {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  height: 103px;
  width: 1200px;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  background-color: #f0f1f2;
  margin-bottom: 2%;
}
.mapInfoLeft {
  display: flex;
  width: 80%;
}
.MapSchool {
  width: 30px;
  height: 30px;
  padding: 2.5%;
}
.address {
  align-items: center;
  display: flex;
}
.addressPoint {
  color: #01348b;
}
.mapBtn {
  width: 103px;
  height: 103px;
  border: none;
  background-color: #01348b;
}
.pingTxt {
  color: white;
  margin-top: 8px;
  font-size: 16px;
}
.busSchedule {
  box-sizing: border-box;
  border: 1px solid #d8d8d8;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  margin-bottom: 80px;
  display: flex;
  align-items: center;
  font-size: 15px;
}
.terminal {
  padding-left: 2%;
  margin-left: 15px;
  border-left: 1px solid #d8d8d8;
  line-height: 30px;
  font-weight: 600;
}
.terminal p {
  margin: 8px 0;
}
.terminal span {
  font-weight: 400;
}
.busIcon {
  width: 64px;
  height: 64px;
  align-items: center;
}

/* 반응형 */
@media (max-width: 1750px) {
  .map {
    width: 900px;
  }
  .map img {
    width: 900px;
  }
  .mapInfo {
    width: 900px;
    margin-bottom: 20px;
  }

  .busSchedule {
    width: 900px;
  }
}
