.title {
  text-align: center;
  border-bottom: #d8d8d8 1px solid;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px;
  line-height: 28px;
  padding-bottom: 24px;
}
.title > p {
  width: 500px;
  margin: 0 auto;
  word-break: keep-all;
}
.alertMsg {
  color: #858585;
  font-size: 16px;
}
.secondTitle {
  text-align: center;
  font-size: 30px;
  font-family: "NanumSquareNeoBold";
  margin: 60px 0 30px;
}
.tableWrap {
  border-top: #01348b 2px solid;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 16px;
  margin-bottom: 0px;
}
.arrow {
  width: 20px;
  height: 20px;
}
.downArrow {
  vertical-align: bottom;
  display: flex;
  justify-content: end;
  padding-right: 70px;
  height: 42px;
}
.downArrow img {
  padding-top: 30px;
}
.infoWrap {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.infoWrap .infoTitle {
  font-size: 26px;
  color: #01348b;
  margin-bottom: 30px;
  margin-top: 60px;
}
.infoWrap li {
  line-height: 35px;
  font-size: 20px;
}

.infoWrap li::marker {
  color: #179bd7;
}
.process {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.process div {
  background-color: #f1f1f1;
  width: 172px;
  height: 172px;
  justify-content: center;
  text-align: center;
  align-items: center;
  display: flex;
  font-size: 20px;
  line-height: 35px;
}

.process .indigoPoint {
  background-color: #01348b;
  color: white;
}
.process .bluePoint {
  background-color: #179bd7;
  color: white;
}
.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
  border: none;
  border-bottom: #858585 1px solid;
}

.searchContainer {
  margin-bottom: 48px;
}

.graphWidth li:nth-child(1) {
  width: 5%;
}
.graphWidth li:nth-child(2) {
  width: 15%;
}

.graphWidth li:nth-child(3) {
  width: 30%;
}
.graphWidth li:nth-child(4) {
  width: 10%;
}
.graphWidth li:nth-child(5),
.graphWidth li:nth-child(6) {
  width: 13%;
}
.graphWidth li:nth-child(7) {
  width: 10%;
}

.detailLink {
  color: black;
}

@media (max-width: 1750px) {
  .title {
    width: 900px;
    font-size: 17px;
  }
  .infoWrap {
    width: 900px;
  }
  .infoWrap .infoTitle {
    font-size: 24px;
  }
  .infoWrap li {
    line-height: 30px;
    font-size: 18px;
  }
  .alertMsg {
    font-size: 15px;
  }

  .process div {
    width: 140px;
    height: 140px;
    font-size: 16px;
    line-height: 30px;
  }
  .downArrow {
    padding-right: 60px;
    height: 46px;
  }
  .downArrow img {
    padding-top: 14px;
  }

  .secondTitle {
    font-size: 26px;
  }
  .tableWrap {
    width: 900px;
  }
  .graphWidth li:nth-child(1) {
    width: 5%;
  }
  .graphWidth li:nth-child(2) {
    width: 25%;
  }
  .graphWidth li:nth-child(3) {
    width: 40%;
  }
  .graphWidth li:nth-child(4) {
    width: 15%;
  }
  .graphWidth li:nth-child(5),
  .graphWidth li:nth-child(7) {
    display: none;
  }
  .graphWidth li:nth-child(6) {
    width: 15%;
  }
}
