.rndTable {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 80px;
}

.rndTh > tr > th,
.rndTb > tr > td {
  padding: 20px 10px;
  border-spacing: 0;
  width: 200px;
  cursor: pointer;
  border-bottom: 1px solid #d8d8d8;
  border-right: 1px solid #d8d8d8;
  text-align: center;
  box-sizing: border-box;
  display: inline-block;
  box-sizing: border-box;
}

.rndTb > tr > td:nth-of-type(-n + 6) {
  border-top: 1px solid #d8d8d8;
}

.rndTb > tr > td:nth-of-type(1),
.rndTb > tr > td:nth-of-type(7),
.rndTb > tr > td:nth-of-type(13) {
  border-left: 1px solid #d8d8d8;
}

.rndTb > tr > td:hover {
  font-weight: 600;
  color: #01348b;
}

.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
}

.graphWidth li:nth-child(1) {
  width: 7%;
}
.graphWidth li:nth-child(2) {
  width: 48%;
}

.graphWidth li:nth-child(3),
.graphWidth li:nth-child(4),
.graphWidth li:nth-child(5) {
  width: 15%;
}

@media (max-width: 1750px) {
  .rndTh > tr > th,
  .rndTb > tr > td {
    width: 180px;
    cursor: pointer;
    border-bottom: 1px solid #d8d8d8;
    border-right: 1px solid #d8d8d8;
  }
  .rndTb > tr > td:nth-of-type(1),
  .rndTb > tr > td:nth-of-type(7),
  .rndTb > tr > td:nth-of-type(13) {
    border-left: none;
  }
  .rndTb > tr > td:nth-of-type(6) {
    border-top: none;
  }

  .rndTb > tr > td:nth-of-type(-n + 5) {
    border-top: 1px solid #d8d8d8;
  }
  .rndTb > tr > td:nth-of-type(1),
  .rndTb > tr > td:nth-of-type(6),
  .rndTb > tr > td:nth-of-type(11),
  .rndTb > tr > td:nth-of-type(16) {
    border-left: 1px solid #d8d8d8;
  }
}
