.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
}

.graphWidth li:nth-child(1) {
  width: 5%;
}
.graphWidth li:nth-child(4) {
  width: 20%;
}
.graphWidth li:nth-child(2),
.graphWidth li:nth-child(3),
.graphWidth li:nth-child(5),
.graphWidth li:nth-child(6),
.graphWidth li:nth-child(7) {
  width: 15%;
}

.graphWidth .studentlink {
  width: 15%;
  color: #000000;
}

.graphWidth .studentlink > li {
  width: 100%;
}

.linkIcon {
  margin-left: 5px;
  font-size: 14px;
  color: #179bd7;
}

.contentWrap {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  background-color: #efefef;
  margin-top: 80px;
  margin-bottom: 80px;
}

.studentContents {
  display: flex;
  width: 1200px;
  margin: 0 auto;
  justify-content: space-between;
}

.contentList {
  width: 180px;
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin: 50px 50px;
}

.studentContents .contentList:nth-child(1) {
  border: 2px solid #01348b;
}
.studentContents .contentList:nth-child(2) {
  border: 2px solid #1866d6;
}
.studentContents .contentList:nth-child(3) {
  border: 2px solid #1b55b2;
}
.studentContents .contentList:nth-child(4) {
  border: 2px solid #01348b;
}

.contentList > div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-color: #ffffff;
  width: 160px;
  height: 160px;
  border-radius: 50%;
}

.contentList > div span {
  font-size: 14px;
  margin: 20px 0 10px;
}

.contentList > div h3 {
  font-size: 18px;
  color: #01348b;
}

.calendarWrap {
  margin-bottom: 80px;
}

.calendarList {
  position: relative;
}

.announcement {
  position: absolute;
  top: 300px;
  left: 720px;
  font-size: 13px;
}

.announcement span {
  background-color: #179bd7;
  width: 34px;
  text-align: center;
  padding: 2px;
  border-radius: 5px;
  color: #ffffff;
  margin-bottom: 5px;
  font-family: "NanumSquareNeoLight";
}

@media (max-width: 1750px) {
  .studentContents {
    width: 900px;
  }
  .contentList {
    margin: 40px 0;
  }
  .contentList > div h3 {
    font-size: 17px;
  }

  .announcement {
    position: absolute;
    top: 300px;
    left: 650px;
    font-size: 13px;
  }

  .announcement span {
    background-color: #179bd7;
    width: 34px;
    text-align: center;
    padding: 2px;
    border-radius: 5px;
    color: #ffffff;
    margin-bottom: 5px;
    font-family: "NanumSquareNeoLight";
  }
}
