.graphWidth {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.title {
  width: 200px;
  border-right: 2px solid #01348b;
  margin-right: 40px;
  font-family: "NanumSquareNeoBold";
}

.graphWidth li:nth-of-type(2) {
  font-weight: 400;
}

.secondTitle {
  margin-left: 100px;
  border-right: 2px solid #01348b;
  font-family: "NanumSquareNeoBold";
  padding-right: 30px;
  margin-right: 30px;
}
.container {
  display: flex;
  flex-direction: column;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  margin-bottom: 60px;
}
.listBtn {
  width: 150px;
  padding: 8px 0px;
  display: inline;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
  border: 1px solid #01348b;
  background-color: white;
  border-radius: 6px;
}

@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .title {
    width: 150px;
    margin-right: 20px;
  }
  .secondTitle {
    margin-left: 40px;
    padding-right: 20px;
    margin-right: 20px;
  }
}
