.container {
  width: 1200px;
  margin: 1% auto;
}

.EquipmentWrap {
  width: 1200px;
  margin-top: 50px;
  border-top: 2px solid #01348b;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.eachEquipment {
  border: solid 2px #d8d8d8;
  width: 350px;
  margin-top: 30px;
  text-align: center;
  cursor: pointer;
}
.equipmentInfo {
  padding: 30px;
  border-top: #d8d8d8 2px solid;
  text-align: left;
}
.equipmentPic {
  width: 270px;
  height: 270px;
}
.equipmentInfo p:nth-of-type(1) {
  font-size: 18px;
}
.equipmentInfo p:nth-of-type(3) {
  font-size: 14px;
  color: #686868;
}
.bold {
  font-family: "NanumSquareNeoBold";
}

.EquipmentWrap {
  display: flex;
}

/* 반응형 */
@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .EquipmentWrap {
    width: 900px;
  }
  .eachEquipment {
    width: 400px;
  }
}
