.container {
  width: 1200px;
  margin: 0 auto;
}

.btnWrap button {
  width: 225px;
}

.title {
  text-align: center;
  font-size: 30px;
  font-family: "NanumSquareNeoBold";
}

.searchContainer {
  margin-top: 30px;
}

.tableWrap {
  border-top: #01348b 2px solid;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 16px;
}

.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
  border: none;
  border-bottom: 1px solid #d8d8d8;
}

.graphWidth li:nth-child(1) {
  width: 5%;
}
.graphWidth li:nth-child(2) {
  width: 30%;
}

.graphWidth li:nth-child(3) {
  width: 15%;
}
.graphWidth li:nth-child(4) {
  width: 10%;
}
.graphWidth li:nth-child(5),
.graphWidth li:nth-child(6) {
  width: 15%;
}
.graphWidth li:nth-child(7) {
  width: 10%;
}

@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .title {
    font-size: 26px;
  }
  .tableWrap {
    width: 900px;
  }
}
