/* graph */
.containerWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.containerWrap h2 {
  color: #01348b;
  font-size: 26px;
  margin: 60px 0 30px;
}
@media (max-width: 1750px) {
  .containerWrap {
    width: 900px;
  }
  .containerWrap h2 {
    font-size: 24px;
  }
}

.graphTitle {
  border-top: 2px solid #01348b;
  font-weight: 600;
}

.graphList {
  border-top: 1px solid #d8d8d8;
  border-bottom: 1px solid #d8d8d8;
}

/* Pagination */
.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  margin: 50px 0 80px;
}

.pagination p {
  font-weight: 600;
  margin: 0 5px;
  color: #179bd7;
}

.paginationIcon {
  margin: 0 5px;
}

/* DetailSearch */
.searchContainer {
  display: flex;
  align-items: center;
  justify-content: right;
  margin-bottom: 30px;
}

.searchDrop {
  position: relative;
  display: inline-block;
}

.searchContent {
  position: absolute;
  background: #f4f4f4;
  width: 200px;
  border-radius: 20px;
  margin-top: 4px;
}

.searchContent p {
  margin: 15px 20px;
  cursor: pointer;
  color: #8a8a8a;
  font-size: 15px;
}

.searchContent p:hover {
  color: #3d3d3d;
  font-family: "NanumSquareNeoBold";
}

.searchBtn {
  background: #f4f4f4;
  font-size: 16px;
  font-family: "NanumSquareNeoRegular";
  height: 40px;
  width: 200px;
  border: none;
  cursor: pointer;
  text-align: left;
  padding-left: 20px;
  border-radius: 30px;
  margin-right: 10px;
}

.searchBtn .triangle {
  margin-left: 110px;
  color: #8a8a8a;
}

.searchBox {
  display: flex;
  align-items: center;
}

.searchBoxBtn {
  height: 40px;
  background: #f4f4f4;
  border-radius: 40px;
  color: #3d3d3d;
  font-size: 16px;
  width: 300px;
  padding-left: 20px;
  border: none;
  display: flex;
  align-items: center;
}

.searchBoxBtn::placeholder {
  color: #c5c5c5;
  font-family: "NanumSquareNeoRegular";
  display: flex;
  align-items: center;
  justify-content: center;
}

.searchBoxIcon {
  position: absolute;
  margin-left: 290px;
  color: #8a8a8a;
}

@media (max-width: 1750px) {
  .searchBtn {
    font-size: 14px;
    width: 180px;
  }
  .searchBoxBtn {
    font-size: 14px;
    width: 250px;
  }
  .searchBoxIcon {
    margin-left: 242px;
  }
}

/*인프라 검색*/
.searchContainer {
  display: flex;
  justify-content: space-between;
  height: 50px;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.searchWrap {
  display: flex;
  position: relative;
}
.searchWrap select {
  width: 100px;
  padding: 10px;
  font-size: 20px;
  text-align: center;
  border: none;
  border-bottom: 1px solid #d8d8d8;
}
.searchWrap input {
  width: 400px;
  border: none;
  border-bottom: 1px solid #d8d8d8;
  font-size: 20px;
  padding: 10px;
}
.sort {
  height: 40px;
  width: 300px;
  padding: 5px;
  font-size: 16px;
  text-align: left;
  border: 1px solid #d8d8d8;
  color: #595959;
}
.searchIcon {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 10px;
  top: 15px;
}
select {
  font-family: "NanumSquareNeoRegular";
}
@media (max-width: 1750px) {
  .searchContainer {
    width: 900px;
  }
  .searchWrap select {
    width: 100px;
    padding: 10px;
    font-size: 16px;
    text-align: center;
    border: none;
    border-bottom: 1px solid #d8d8d8;
  }
  .searchWrap input {
    width: 220px;
    font-size: 16px;
  }
  .sort {
    width: 220px;
    font-size: 16px;
  }
  .searchIcon {
    top: 10px;
    width: 18px;
    height: 18px;
  }
}

/*btn wrap*/
.btnWrap {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  border-bottom: #d8d8d8 1px solid;
  border-top: #d8d8d8 1px solid;
  padding: 25px 0;
  margin-bottom: 52px;
  margin-top: 40px;
}
.btnWrap button {
  box-sizing: border-box;
  border: none;
  background-color: white;
  cursor: pointer;
  font-size: 20px;
}

@media (max-width: 1750px) {
  .btnWrap button {
    font-size: 17px;
  }
  .btnWrap button:hover {
    font-weight: 600;
    color: #01348b;
  }
}
