header {
  width: 100%;
  position: absolute;
  z-index: 999;
}

.headerContainer {
  width: 100%;
  display: flex;
  justify-content: left;
}

.headerContainerhover {
  background: #ffffff;
}

.menuLinkHover {
  color: #353535;
  font-weight: 600;
}

.headerContainer .menuIcon {
  width: 80px;
  height: 80px;
  min-width: 80px;
  background: #01348b;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.headerInner {
  width: 1700px;
  display: flex;
  justify-content: space-between;
  margin: 0 70px;
  align-items: center;
}

.headerInner h1 img {
  margin-top: 8px;
}

.menu {
  display: flex;
  text-align: center;
}

.menu .mainMenu {
  /* font-size: 17px; */
  width: 170px;
  font-weight: 500;
  position: relative;
  height: 80px;
  line-height: 80px;
}

.menu .mainMenu a {
  display: block;
}

.menu .mainMenu:hover {
  box-sizing: border-box;
  border-bottom: 3px solid #01348b;
  border-image: linear-gradient(to right, #179bd7, #01348b) 1;
}

.menu .mainMenu .subMain {
  display: none;
  width: 100%;
  position: absolute;
  padding: 10px 0;
}

.menu .mainMenu .subMain a {
  color: #353535;
}

.menu .mainMenu .subMain a:hover {
  color: #01348b;
}

.menu .mainMenu .subMainList {
  font-size: 14px;
  font-weight: 600;
  line-height: 32px;
}

.menu .mainMenu:hover .subMain {
  display: block;
  background: #ffffffc4;
}

.headerRight {
  display: flex;
}

.headerRight img {
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.headerRight .loginBtn {
  height: 50px;
  width: 100px;
  background: #01348b;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 40px;
  color: #ffffff;
  font-size: 14px;
  margin-left: 20px;
  cursor: pointer;
}

.headerRight .loginBtn span {
  margin-left: 8px;
}

/* 전체메뉴 */
.allMenu {
  top: 80px;
  width: 100%;
  background: #ffffff;
  z-index: 9999;
  position: absolute;
  display: none;
  border-top: 1px solid #dbdbdb63;
  border-bottom: 1px solid #dbdbdb63;
}

.allMenuShow {
  display: block;
}

.allMenuInner {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  text-align: left;
  padding-top: 40px;
}

.allMenuInner a {
  color: #353535;
}

.allMainMenu {
  margin: 0 30px;
}

.allMainMenu > a {
  font-weight: 600;
  font-size: 17px;
}

.allMainMenu > a:after {
  content: "";
  display: block;
  width: 40px;
  border-bottom: 2px solid #179bd7;
  margin: 20px 0;
}

.allSubMain {
  font-size: 16px;
  line-height: 30px;
  margin: 20px 0 40px;
}

.menu .mainMenu .subMainList {
  margin: 6px 0;
  word-break: keep-all;
}

.hideSubMenu {
  display: none;
}

/* 검색 */
.searchBox {
  position: absolute;
  width: 100%;
  height: 180px;
  background: #f4f4f4;
  top: 80px;
  display: none;
}

.search {
  width: 100%;
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search label {
  font-size: 18px;
  margin-right: 20px;
}

.search .searchBtn::placeholder {
  color: #ffffff;
  font-family: "NanumSquareNeoLight";
  display: flex;
  align-items: center;
  justify-content: center;
}

.search .searchIcon {
  color: #ffffff;
  font-size: 22px;
  cursor: pointer;
  margin-left: -40px;
}

.searchBtn {
  background-color: #179bd7;
  border-radius: 40px;
  color: #ffffff;
  font-size: 18px;
  width: 650px;
  height: 50px;
  padding-left: 20px;
  border: none;
  display: flex;
  align-items: center;
}

.search .searchBtn:focus {
  border: none;
  outline: none;
}

.searchBoxShow {
  display: block;
}

/* 반응형 */
@media (max-width: 1750px) {
  .menu {
    position: absolute;
    width: 100%;
    top: 80px;
    left: 0;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    border-top: 1px solid #dbdbdb63;
    box-sizing: border-box;
  }
  .menu .mainMenu .subMainList {
    line-height: 20px;
    margin: 15px 0;
    word-break: keep-all;
  }

  .headerRight > img {
    display: none;
  }
  .searchBox {
    display: block;
    width: 300px;
    height: 80px;
    background: none;
    top: 0;
    right: 208px;
  }
  .search {
    width: 300px;
    height: 80px;
    justify-content: right;
  }
  .search label {
    display: none;
  }
  .search .searchBtn::placeholder {
    font-size: 14px;
  }
  .search .searchIcon {
    font-size: 16px;
  }
  .searchBtn {
    background-color: #179bd7;
    width: 250px;
    height: 40px;
  }
  .headerRight .loginBtn {
    height: 40px;
    width: 100px;
    background: #01348b;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    color: #ffffff;
    font-size: 14px;
    margin-left: 20px;
    cursor: pointer;
  }

  .allMainMenu {
    margin: 0 10px;
    width: 14%;
    word-break: keep-all;
  }
  .allMainMenu > a {
    font-size: 15px;
  }
  .allSubMain {
    font-size: 14px;
    line-height: 20px;
    margin: 20px 0 30px;
  }
  .allSubMain > li {
    margin: 10px 0;
  }
}
