.subTitle {
  width: 800px;
  margin: 0 auto;
  text-align: center;
  word-break: keep-all;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 24px;
}

.containerWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.containerWrap div .marginEdit {
  margin-bottom: 20px;
}

.liMarker {
  font-size: 20px;
  line-height: 30px;
  list-style-position: inside;
  text-indent: -26px;
  margin-left: 26px;
}

.liMarker::marker {
  color: #179bd7;
}

.stepIcc {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stepIcc > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1200px;
}

.stepIcc > div:nth-of-type(1) img {
  margin-left: 298px;
}
.stepIcc > div:nth-of-type(2) img {
  margin-right: 706px;
}
.stepIcc > div:nth-of-type(3) img {
  margin-left: 267px;
}

.stepIcc > div:nth-of-type(1) .stepTxt {
  margin-left: 230px;
}
.stepIcc > div:nth-of-type(2) .stepTxt {
  margin-right: 10px;
}
.stepIcc > div:nth-of-type(3) .stepTxt {
  margin-left: 510px;
}

.stepIcc div img {
  padding: 10px 0;
}

.stepIcc div .stepTxt {
  position: absolute;
  width: 800px;
  padding: 10px 0;
}

.stepIcc div .stepTxt span {
  font-size: 16px;
  color: #179bd7;
}

.stepIcc div .stepTxt h4 {
  font-size: 24px;
  margin: 5px 0 30px;
}

.stepIcc div .stepTxt p {
  font-size: 18px;
  line-height: 35px;
  width: 720px;
  word-break: keep-all;
}

.supportIcc {
  margin-bottom: 50px;
}

.supportIcc h4 {
  font-size: 20px;
  margin-bottom: 20px;
}

.supportIccList {
  border: 1px solid #d8d8d8;
  display: flex;
  justify-content: center;
}

.supportIccList > div {
  display: flex;
  margin: 0 70px;
  font-size: 18px;
  line-height: 32px;
  padding: 20px 0;
}

.supportIccList > div strong {
  margin-right: 20px;
}

.supportIccList > div strong:after {
  content: "";
  position: absolute;
  width: 3px;
  border-bottom: 14px solid #179bd7;
  margin: 9px;
}

.iccNewsList {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
}

.iccNews {
  width: 380px;
  height: 510px;
  border-radius: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.iccNews img {
  border-radius: 20px 20px 0 0;
}

.iccNews > div {
  padding: 15px;
  word-break: keep-all;
}

.iccNews > div h4 {
  font-size: 18px;
  margin-bottom: 20px;
}

.iccNews > div p {
  font-size: 15px;
  line-height: 22px;
}

.iccNews > div p em {
  font-style: normal;
  background: linear-gradient(to top, #81c9e2 40%, transparent 40%);
}

@media (max-width: 1750px) {
  .subTitle {
    font-size: 17px;
  }
  .containerWrap {
    width: 900px;
  }
  .liMarker {
    font-size: 18px;
    word-break: keep-all;
  }
  .stepIcc > div {
    width: 900px;
  }
  .stepIcc > div:nth-of-type(1) img {
    width: 630px;
    margin-left: 239px;
  }
  .stepIcc > div:nth-of-type(2) img {
    width: 350px;
    margin-right: 550px;
  }
  .stepIcc > div:nth-of-type(3) img {
    width: 650px;
    margin-left: 220px;
  }
  .stepIcc > div:nth-of-type(1) .stepTxt {
    margin-left: 380px;
  }
  .stepIcc > div:nth-of-type(2) .stepTxt {
    margin-right: -190px;
  }
  .stepIcc > div:nth-of-type(3) .stepTxt {
    margin-left: 570px;
  }
  .stepIcc > div:nth-of-type(3) .stepTxt p {
    width: 500px;
  }
  .stepIcc div .stepTxt {
    width: 800px;
  }
  .stepIcc div .stepTxt span {
    font-size: 14px;
  }
  .stepIcc div .stepTxt h4 {
    font-size: 20px;
    margin: 5px 0 10px;
  }
  .stepIcc div .stepTxt p {
    font-size: 16px;
    line-height: 28px;
    width: 640px;
  }

  .supportIccList > div {
    margin: 0 40px;
    font-size: 16px;
    line-height: 28px;
  }
  .supportIccList > div strong:after {
    margin: 7px 9px;
  }

  .iccNews {
    width: 285px;
    height: auto;
  }
  .iccNews img {
    width: 285px;
  }
  .iccNews > div {
    padding: 10px;
  }
  .iccNews > div h4 {
    font-size: 17px;
    line-height: 24px;
  }
  .iccNews > div p {
    font-size: 14px;
  }
}
