.containerWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.projectTable {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 80px;
  border: 1px solid #d8d8d8;
}

.projectTh > tr > th {
  background-color: #e9e9e9;
}

.projectTh > tr > th,
.projectTb > tr > td {
  padding: 10px;
  border-spacing: 0;
  border: 1px solid #d8d8d8;
  text-align: center;
}
