.container {
  width: 1200px;
  margin: 0 auto;
}

.eachTeam {
  font-size: 20px;
  cursor: pointer;
  width: 171px;
}

.eachTeam:hover {
  color: #01348b;
  font-weight: 600;
}

.tableWrap {
  margin-left: auto;
  margin-right: auto;
  margin-top: 60px;
  width: 1200px;
  margin-bottom: 80px;
  border-top: 2px solid #01348b;
  border-collapse: collapse;
  line-height: 25px;
}
.duty {
  text-align: left;
  border-right: none;
}
.number {
  width: 27%;
}
.leftEnd {
  border-left: none;
}
.rightEnd {
  border-right: none;
}
.tableTitle {
  padding: 1% 2%;
}
.tableWrap .name {
  width: 10%;
}
.tableWrap th,
.tableWrap td {
  border: 1px solid #d8d8d8;
  padding: 20px 10px;
  text-align: center;
}
.tableWrap th {
  background-color: #f0f1f2;
}

/* 반응형 */
@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .eachTeam {
    font-size: 17px;
    width: 128px;
  }
  .tableWrap {
    width: 900px;
  }
}
