.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.meaning {
  box-sizing: border-box;
  text-align: center;
  border-bottom: 1px solid #d8d8d8;
  font-size: 18px;
  line-height: 28px;
  padding-bottom: 24px;
}

.title {
  font-size: 26px;
  color: #01348b;
  margin: 60px 0 30px;
}
.titleMargin {
  margin: 20px 0 30px;
}
.tableWrap th,
.tableWrap td {
  border: 1px solid #d8d8d8;
  padding: 2%;

  text-align: left;
}
.tableWrap th {
  background-color: #f0f1f2;
  font-family: "NanumSquareNeoBold";
  font-size: 18px;
  text-align: center;
}
.leftEnd {
  width: 300px;
}
.content {
  width: 130px;
}
.tableWrap {
  margin-left: auto;
  margin-right: auto;
  width: 1200px;
  margin-bottom: 5%;
  border-collapse: collapse;
  line-height: 25px;
}

.compensation {
  border: #d8d8d8 1px solid;
  padding: 25px;
  margin-bottom: 80px;
}

.compensation p {
  line-height: 25px;
  margin-bottom: 40px;
}

.compensation li {
  line-height: 25px;
}

.box {
  box-sizing: border-box;
  border: #d8d8d8 1px solid;
  margin-bottom: 40px;
  width: 270px;
  justify-content: center;
  height: 200px;
  flex-direction: column;
  padding-left: 15px;
}

.box p {
  font-size: 18px;
  margin-bottom: 20px;
}
.box li {
  font-size: 14px;
  line-height: 30px;
}

.process1,
.process2 {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.box1 {
  border-top: 2px solid #4486f8;
}
.box2 {
  border-top: 2px solid #0962fb;
}
.box3 {
  border-top: 2px solid #003796;
}
.box4 {
  border-top: 2px solid #02286a;
}
.numWrap {
  display: flex;
  justify-content: end;
}
.number {
  text-align: end;
  color: white;
  padding: 5px 20px;
  display: block;
  width: 30px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  text-align: center;
}
.color1 {
  background-color: #4486f8;
}
.color2 {
  background-color: #0962fb;
}
.color3 {
  background-color: #003796;
}
.color4 {
  background-color: #02286a;
}

@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .meaning {
    font-size: 17px;
  }
  .title {
    font-size: 24px;
  }
  .tableWrap th {
    font-size: 16px;
  }
  .leftEnd {
    width: 200px;
  }
  .tableWrap {
    width: 900px;
  }

  .box {
    width: 210px;
    height: 165px;
    padding: 0 10px;
  }
  .box p {
    font-size: 15px;
    margin-bottom: 10px;
    font-weight: 600;
  }
  .box li {
    line-height: 20px;
    list-style: none;
    word-break: keep-all;
    margin: 5px 0;
  }
  .color1 {
    background-color: #4486f8;
  }
  .color2 {
    background-color: #0962fb;
  }
  .color3 {
    background-color: #003796;
  }
  .color4 {
    background-color: #02286a;
  }
}
