.subTitle {
  width: 900px;
  margin: 0 auto;
  text-align: center;
  word-break: keep-all;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 24px;
}

.containerWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.corporateTable {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 80px;
  border: 1px solid #d8d8d8;
  word-break: keep-all;
  margin-top: 60px;
}

.corporateTh > tr > th,
.corporateTb > tr > th {
  background-color: #e9e9e9;
  border: 1px solid #d8d8d8;
  line-height: 24px;
}

.corporateTh > tr > th,
.corporateTb > tr > td {
  padding: 12px;
  border-spacing: 0;
  border: 1px solid #d8d8d8;
  text-align: center;
  line-height: 24px;
}

.corporateTb > tr > td {
  text-align: left;
  height: 50px;
}

.corporateTb > tr > th {
  width: 200px;
  padding: 0 20px;
}

.step01,
.step02 {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

.step01 > div > p,
.step02 > p {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 70px;
  font-size: 18px;
  color: #ffffff;
  background: #f0f1f2;
}

.step01 > div:nth-of-type(1) > p {
  background-color: #01348b;
}
.step01 > div:nth-of-type(2) > p {
  background-color: #179bd7;
}
.step01 > div:nth-of-type(3) > p {
  background-color: #00809e;
}

.step01 > div > img {
  margin: 10px 0;
}

.corporateSystem > h3 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70px;
  border: 1px solid #d8d8d8;
  font-size: 18px;
  margin-bottom: 40px;
}

.step02 > p {
  color: #000000;
  font-weight: 600;
}

.step02 > div {
  margin-top: 9px;
}

.step02 > div > span {
  font-size: 14px;
  color: #999999;
}

.step03 {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-top: 40px;
}

.step03 > p {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 360px;
  height: 70px;
  background: #ff7300;
  color: #ffffff;
  font-size: 18px;
  margin: 0 10px 70px;
}

.step03 > span {
  font-size: 14px;
  margin: 0 10px;
  color: #999999;
}

.step03 img:nth-of-type(2) {
  transform: scaleX(-1);
}

.step04,
.step06 {
  display: flex;
  justify-content: space-between;
}

.step04 p,
.step06 p {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 270px;
  height: 70px;
  border: 1px solid #d8d8d8;
  box-sizing: border-box;
  font-size: 18px;
  margin-top: 10px;
  font-weight: 600;
  padding: 0 20px;
  text-align: center;
  word-break: keep-all;
  line-height: 30px;
}

.step04 > div {
  position: absolute;
}

.step05 {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-top: -126px;
}

.step05 > div {
  margin: 0 142px;
}

.step05 > div > em {
  font-size: 14px;
  color: #999999;
}

.arrowBg {
  margin-top: 60px;
}

.step06 {
  margin: 20px 0 80px;
}

@media (max-width: 1750px) {
  .subTitle {
    font-size: 17px;
  }
  .containerWrap {
    width: 900px;
  }
  .step01 > div > p,
  .step02 > p {
    width: 250px;
    height: 60px;
    font-size: 16px;
  }
  .corporateSystem > h3 {
    height: 60px;
    font-size: 16px;
  }
  .step02 > div {
    margin-top: 5px;
  }
  .step02 > div > span {
    font-size: 13px;
  }
  .step01 > div > img {
    width: 20px;
  }
  .step02 > div > img {
    width: 100px;
    margin: 0 10px;
  }
  .step03 > img {
    width: 210px;
  }
  .step05 > div > img {
    width: 20px;
  }
  .step03 > p {
    width: 240px;
    height: 60px;
    font-size: 16px;
    margin-bottom: 53px;
  }
  .step03 > span {
    font-size: 13px;
    width: 70px;
  }
  .step04 p,
  .step06 p {
    width: 200px;
    height: 60px;
    font-size: 16px;
    padding: 0px;
    line-height: 28px;
  }
  .step05 {
    margin-top: -100px;
  }
  .step05 > div {
    margin: 0 142px;
    display: flex;
    align-items: end;
  }
  .step05 > div > em {
    font-size: 13px;
  }
  .arrowBg {
    width: 900px;
    margin-top: 60px;
  }
}
