.infoWrap .infoTitle {
  font-size: 26px;
  color: #01348b;
  margin-bottom: 30px;
  margin-top: 60px;
}
.infoWrap li {
  line-height: 35px;
  font-size: 20px;
}

.infoWrap li::marker {
  color: #179bd7;
}

.container {
  border-top: 1px solid #d8d8d8;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.process .imgWrap {
  border: #797979 dotted 2px;
  padding: 40px;
  border-radius: 75%;
}

.processWrap {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
}

.process .title {
  font-size: 20px;
  font-family: "NanumSquareNeoBold";
  text-align: center;
  margin-top: 52px;
}

.title:after {
  content: "";
  display: block;
  width: 60px;
  border-bottom: 3px solid #179bd7;
  margin: 30px auto;
}

.process .info {
  font-size: 17px;
  text-align: center;
  margin-top: 30px;
  line-height: 27px;
}

@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .infoWrap .infoTitle {
    font-size: 24px;
  }
  .infoWrap li {
    line-height: 30px;
    font-size: 18px;
  }
  .process .imgWrap {
    width: 90px;
    height: 90px;
  }
  .process .imgWrap > img {
    width: 100px;
  }
  .process .title {
    font-size: 18px;
    margin-top: 32px;
  }
  .title:after {
    width: 40px;
    margin: 20px auto;
  }
  .process .info {
    font-size: 16px;
    margin-top: 20px;
  }
}
