.firstWrap {
  display: flex;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
}
.firstWrap .mainImg {
  width: 300px;
}
.imgWrap {
  width: 330px;
}
.subImgWrap {
  display: flex;
  justify-content: space-between;
}
.subImg {
  width: 100px;
  border: solid 1px #d8d8d8;
}

.eachInfo h4,
.eachInfo p {
  display: inline-block;
}

.eachInfo {
  width: 700px;
  box-sizing: border-box;
  border-bottom: solid 1px #d8d8d8;
  padding: 15px 0px;
  display: flex;
  align-items: center;
}
.eachInfo h4 {
  width: 100px;
  border-right: 2px solid #01348b;
  align-items: center;
}
.eachInfo p {
  width: 300px;
  font-size: 14px;
  padding-left: 20px;
}
.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  border-top: 2px solid #01348b;
  padding-top: 10px;
}

.secondWrap {
  box-sizing: border-box;
  width: 1200px;
  background-color: #f7f7f7;
  padding-top: 50px;
  padding-left: 50px;
  padding-right: 50px;
  margin-top: 50px;
  margin-bottom: 60px;
}

.titleWrap {
  display: flex;
}

.titleWrap h3 {
  margin-left: 20px;
  font-size: 16px;
}

.titleWrap img {
  width: 20px;
  height: 15px;
}

.secondWrap p {
  font-size: 14px;
  line-height: 26px;
  margin-top: 20px;
  margin-bottom: 50px;
}

.totalWrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.totalWrap .left {
  display: flex;
}
.quantity {
  box-sizing: border-box;
  display: flex;
  padding-top: 20px;
}
.quantity h4,
.usedWrap h4 {
  width: 150px;
}

.quantity .controlBtn {
  display: flex;
  border: 1px solid #d8d8d8;
  border-radius: 5px;
}

.quantity button {
  background-color: white;
  border-radius: 50px;
  border: none;
  width: 30px;
  height: 25px;
}
.quantity p {
  padding: 5px 15px;
  border-left: 1px solid #d8d8d8;
  border-right: 1px solid #d8d8d8;
  font-size: 14px;
}
.warning {
  padding-left: 150px;
  font-size: 12px;
  color: #595959;
  padding-bottom: 25px;
  border-bottom: 1px solid #d8d8d8;
  margin-bottom: 25px;
}
.usedWrap {
  display: flex;
  padding: 20px 0px 10px 0px;
}

.usedWrap p {
  margin-left: 10px;
  padding-top: 5px;
}

.usedWrap input {
  box-sizing: border-box;
  border: 1px solid #d8d8d8;
  width: 100px;
  padding: 5px 8px;
  border-radius: 5px;
  text-align: right;
}
.left span {
  display: inline;
  color: #01348b;
  font-size: 20px;
}

.right button {
  margin-left: 20px;
  padding: 12px 40px;
}
.right .cart {
  border: #01348b 1px solid;
  background-color: white;
}
.right .buy {
  color: white;
  background-color: #01348b;
  border: #01348b 1px solid;
  border: none;
}
.totalWrap h4 {
  padding-right: 40px;
}

.projectTable {
  margin-top: 20px;
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 80px;
  background-color: white;
  border: 1px solid #d8d8d8;
}

.projectTh > tr > th {
  background-color: #e9e9e9;
}

.projectTh > tr > th,
.projectTb > tr > td {
  padding: 10px;
  border-spacing: 0;
  border: 1px solid #d8d8d8;
  text-align: center;
}

@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .firstWrap {
    width: 900px;
  }
  .eachInfo {
    width: 530px;
    padding: 13px 0px;
  }
  .right button {
    padding: 12px 30px;
  }
  .secondWrap {
    width: 900px;
  }
}
