.container {
  width: 400px;
  margin: 50px auto 0;
}

.joinForm {
  text-align: center;
  width: 100%;
  height: 868px;
  border: 1px solid #b5b5b554;
  border-radius: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, 0.1);
}

.joinForm > img {
  margin: 30px 0;
}

.joinForm h2 {
  font-size: 20px;
  margin-bottom: 20px;
}

.inputWrap {
  display: flex;
  flex-flow: column;
}

.inputForm {
  text-align: left;
  margin: 0 auto;
}

.inputForm p {
  font-size: 16px;
  margin: 15px 0 0px;
}

input {
  border: none;
  outline: none;
}

.inputForm input {
  width: 300px;
  height: 35px;
  border-bottom: 1px solid #b5b5b5;
}

.inputForm > em {
  position: absolute;
  font-size: 12px;
  background: #01348b;
  padding: 5px 10px;
  color: #ffffff;
  border-radius: 15px;
  margin: 8px 0 0 -65px;
}

.inputForm > em:nth-of-type(2) {
  margin: 7px 0 0 -90px;
}

input::placeholder {
  font-family: "NanumSquareNeoLight";
}

.agreement {
  width: 300px;
  text-align: left;
  margin: 20px auto;
}

.agreement > div {
  font-size: 14px;
  display: flex;
  margin: 2px 0;
  align-items: center;
  justify-content: left;
}

.agreement > div > input {
  width: 12px;
  height: 12px;
  margin-right: 5px;
}

.agreement > div > p {
  margin-top: 4px;
}

.agreement > div:nth-of-type(1) > p {
  font-weight: 600;
}

.joinButton {
  background: #01348b;
  border: none;
  width: 300px;
  height: 40px;
  color: #ffffff;
  font-family: "NanumSquareNeoRegular";
  font-size: 16px;
  margin: 20px auto;
  cursor: pointer;
}
