.contentLayout {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px 0;
}

.contentLayout .title {
  font-family: NanumSquareNeoBold;
  font-size: 25px;
  color: #333;
  margin-bottom: 60px;
}

.contentLayout .children {
  width: 1200px;
}

.sectionPhotoNews {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.sectionPhotoNews .card {
  width: 346px;
  height: 380px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 3px 8px 4px #eaeaea;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.sectionPhotoNews .card:nth-child(2) {
  margin: 0 81px;
}

.sectionPhotoNews .card img {
  width: 100%;
  border-radius: 10px 10px 0 0;
}

.sectionPhotoNews .card .contentWrap {
  flex: 1;
  width: 100%;
  padding: 18px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #333;
}

.sectionPhotoNews .card .contentWrap .contentTitle {
  font-family: NanumSquareNeoBold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sectionPhotoNews .card .contentWrap .content {
  font-size: 14px;
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.sectionPhotoNews .card .contentWrap .additional {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.sectionPhotoNews .card .contentWrap .additional .date {
  font-size: 12px;
  color: #999;
}

.sectionPhotoNews .card .contentWrap .additional .arrow {
  font-size: 14px;
  color: #555;
}

.sectionInfo {
  width: 100%;
  height: 400px;
  display: flex;
  position: relative;
  justify-content: center;
  overflow: hidden;
}

.sectionInfo .contentWrap {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
}

.sectionInfo .contentWrap .content {
  width: 1200px;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.sectionInfo .contentWrap .left {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  width: 280px;
}

.sectionInfo .contentWrap .left .titleWrap {
  display: flex;
  align-items: center;
}

.sectionInfo .contentWrap .left .titleWrap img {
  width: 48px;
  margin-right: 20px;
}

.sectionInfo .contentWrap .left .titleWrap span {
  font-family: NanumSquareNeoBold;
  font-size: 28px;
}

.sectionInfo .contentWrap .left .middle {
  margin: 36px 0;
  font-family: NanumSquareNeoLight;
  font-size: 17px;
  line-height: 26px;
}

.sectionInfo .contentWrap .left .detail {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.sectionInfo .contentWrap .left .detail .arrow {
  margin-left: 10px;
}

.sectionInfo .contentWrap .right {
  flex: 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: white;
}

.sectionInfo .contentWrap .right .element {
  width: 235px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 32px;
}

.sectionInfo .contentWrap .right .element img {
  height: 48px;
  margin-bottom: 25px;
}

.sectionInfo .contentWrap .right .element .title {
  font-size: 18px;
}

.sectionInfo .contentWrap .right .element .divider {
  width: 100%;
  height: 2px;
  background-color: white;
  margin: 20px 0;
}

.sectionInfo .contentWrap .right .element .des {
  font-family: NanumSquareNeoLight;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  font-size: 13px;
  margin: 10px 0;
  cursor: pointer;
}

.sectionInfo .contentWrap .right .element .more {
  width: 26px;
  height: 26px;
  margin-top: 20px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px #bbb;
  background-color: #fbfbfb;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(43, 148, 198);
  font-size: 17px;
  cursor: pointer;
}

.sectionVideo {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.sectionVideo .playerWrapper {
  position: relative;
  padding-top: 27%;
  width: 48%;
}

.sectionVideo .playerWrapper .reactPlayer {
  position: absolute;
  top: 0;
  left: 0;
}

.sectionVideo.titleWrap {
  margin-top: 25px;
}

.sectionVideo.titleWrap .videoTitle {
  width: 48%;
  text-align: center;
  font-size: 15px;
  color: #333;
}

.sectionReference {
  width: 100%;
  height: 100px;
  border-top: 1px solid #83838367;
  display: flex;
  justify-content: center;
}

.sectionReference .wrap {
  width: 1200px;
  display: flex;
  height: 100%;
  align-items: center;
}

.sectionReference .wrap .title {
  color: "#333";
  margin-right: 25px;
}

.sectionReference .wrap img {
  height: 45px;
  cursor: pointer;
}

/* 반응형 */
@media (max-width: 1750px) {
  .contentLayout .children {
    width: 980px;
  }
  .sectionPhotoNews .card {
    width: 280px;
    height: 340px;
  }
  .sectionPhotoNews .card:nth-child(2) {
    margin: 0;
  }

  .sectionInfo .contentWrap .content {
    width: 980px;
  }
  .sectionInfo .contentWrap .left {
    width: 270px;
    word-break: keep-all;
  }
  .sectionInfo .contentWrap .right .element {
    width: 215px;
    margin-left: 20px;
  }

  .sectionReference {
    height: 80px;
  }
  .sectionReference .wrap {
    width: 980px;
  }
}
