.selectWrap select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 26%;
  padding-right: 10px;
  height: 35px;
  border: none;
  border-bottom: 2px solid #d8d8d8;
  vertical-align: middle;
}

.selectWrap select::-ms-expand {
  display: none;
}

.container .imageWrap .infoPic {
  height: 450px;
  width: 100%;
}
.container .imageWrap {
  margin-top: 80px;
  position: relative;
}
.dropDownWrap {
  background-color: rgb(255, 255, 255);
  width: 1200px;
  top: 108%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
.dropDownWrap .selectWrap .nextIcon {
  width: 9px;
  height: 17px;
  margin-top: 1%;
  margin-left: 5%;
  margin-right: 5%;
}

.dropDownWrap .selectWrap .homeIcon {
  width: 25px;
  height: 25px;
  margin-top: 0.5%;
}

.dropDownWrap .selectWrap {
  box-sizing: border-box;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  height: 80px;
  position: relative;
}

.selectWrap .IconWrap {
  text-align: center;
  line-height: 77px;
  display: flex;
  justify-content: center;
  padding-top: 2%;
}
.caretDown {
  font-size: 17px;
  color: #b6b6b6;
  margin-top: 5px;
  margin-left: -15px;
}
.dropDownWrap .titleWrap {
  justify-content: center;
  /* border-bottom: #d8d8d8 1px solid; */
}
.textWrap {
  color: white;
  position: absolute;
  width: 100%;
  text-align: center;
  top: 43%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.textWrap .title {
  text-align: center;
  margin-bottom: 26px;
  font-family: "NanumSquareNeoBold";
  font-size: 40px;
}
.textWrap .subTitle {
  font-family: "NanumSquareNeoRegular";
  font-size: 24px;
}

.subtitleWrap .iconPoint {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 4%;
}
.subtitleWrap .infoTitle {
  text-align: center;
  font-size: 36px;
  margin-top: 28px;
  margin-bottom: 25px;
  font-family: "NanumSquareNeoBold";
}
.mainCategory {
  color: #898989;
  font-family: "NanumSquareNeoBold";
  font-size: 20px;
  padding-bottom: 3px;
}
.subCategory {
  color: #01348b;
  font-family: "NanumSquareNeoBold";
  font-size: 20px;
  padding-bottom: 3px;
}

/* 반응형 */
@media (max-width: 1750px) {
  .dropDownWrap {
    width: 900px;
  }
  .container .imageWrap {
    margin-top: 160px;
  }
  .subtitleWrap .infoTitle {
    font-size: 30px;
  }
  .mainCategory {
    font-size: 18px;
  }
  .subCategory {
    font-size: 18px;
  }
}
