.subTitle {
  width: 900px;
  margin: 0 auto;
  text-align: center;
  word-break: keep-all;
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 24px;
}

.containerWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.mileageSearchBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mileageSearchBoxBtn {
  height: 40px;
  border: 1px solid #d8d8d8;
  padding: 0;
  box-sizing: border-box;
  font-family: "NanumSquareNeoRegular";
  flex-grow: 1;
  margin-right: 20px;
  padding: 10px;
}

.mileageButton {
  width: 200px;
  height: 40px;
  border: none;
  padding: 0;
  background: #01348b;
  font-family: "NanumSquareNeoRegular";
  color: #ffffff;
  font-size: 17px;
  cursor: pointer;
}

.mileageTop > p {
  display: flex;
  font-size: 22px;
  padding: 26px;
  margin: 20px 0;
  background: #f2f2f2;
  justify-content: center;
  align-items: center;
}

.mileageTop > p span {
  font-size: 26px;
  color: #179bd7;
  font-weight: 600;
  margin: 0 30px;
}

.mileageIcon {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  text-align: center;
}

.mileageIcon > div {
  padding: 10px 40px;
  border: 1px solid #d8d8d8;
  margin-bottom: 30px;
  cursor: pointer;
}

.mileageIcon > div p {
  font-size: 14px;
  margin-top: 5px;
}

form {
  width: 100%;
  font-size: 18px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 70px;
}

form > div {
  margin: 7px 0;
}

form > div > em {
  margin-right: 20px;
  display: inline-block;
  width: 120px;
  font-style: normal;
}

form > div > input {
  height: 40px;
  width: 210px;
  border: 1px solid #d8d8d8;
  box-sizing: border-box;
  font-family: "NanumSquareNeoRegular";
  padding: 10px;
}

form > div:nth-of-type(2) > input {
  width: 441px;
}
form > div:nth-of-type(5) > input {
  width: 800px;
}

form > div > button {
  width: 125px;
  height: 40px;
  border: none;
  padding: 0;
  background: #01348b;
  font-family: "NanumSquareNeoRegular";
  color: #ffffff;
  font-size: 17px;
  cursor: pointer;
  margin-left: 5px;
}

form > div > button:nth-of-type(2) {
  background: #dbdbdb;
  color: #000000;
  font-weight: 600;
}

em {
  font-style: normal;
}

.mileageItem > p {
  border-bottom: 2px solid #01348b;
  padding-bottom: 10px;
}

.mileageItem > p > em {
  color: #179bd7;
}

.mileageItemList {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.mileageItemList > div {
  width: 250px;
  height: 400px;
  border: 1px solid #d8d8d8;
  margin: 40px 25px 0;
  box-sizing: border-box;
}

.mileageItemList > div > a > div {
  padding: 10px;
  color: #000000;
}

.mileageItemList > div > a > div h4 {
  font-size: 16px;
  margin-bottom: 24px;
}

.mileageItemList > div > a > div p:nth-of-type(1) {
  display: flex;
  font-size: 14px;
  align-items: center;
}

.mileageItemList > div > a > div p:nth-of-type(1) em {
  border: 1px solid #179bd7;
  border-radius: 50%;
  background: #179bd7;
  padding: 3px 4px;
  font-size: 12px;
  margin-right: 3px;
}

.mileageItemList > div > a > div p:nth-of-type(2) {
  font-weight: 600;
  margin: 12px 0;
}

.mileageItemList > div > a > div p:nth-of-type(2) strong {
  font-weight: 200;
  color: #a0a0a0;
  text-decoration: line-through;
  margin-right: 10px;
}

.mileageItemList > div > a > div p:nth-of-type(3) {
  font-size: 13px;
  color: #686868;
}

@media (max-width: 1750px) {
  .subTitle {
    font-size: 17px;
  }
  .containerWrap {
    width: 900px;
  }
  .mileageSearchBox {
    width: 900px;
  }
  .mileageButton {
    font-size: 16px;
  }
  .mileageTop > p {
    font-size: 20px;
  }
  .mileageTop > p span {
    font-size: 24px;
  }
  .mileageIcon > div {
    padding: 10px 24px;
  }

  form {
    font-size: 17px;
  }
  form > div > em {
    margin-right: 0;
    width: 100px;
  }
  form > div > input {
    width: 160px;
  }
  form > div:nth-of-type(2) > input {
    width: 340px;
  }
  form > div:nth-of-type(5) > input {
    width: 540px;
  }
  form > div > button {
    font-size: 16px;
  }
}
