.industryWrap {
  margin: 0 auto;
  width: 1200px;
  border-top: 1px solid #d8d8d8;
}

.searchContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ececec;
  height: 140px;
  margin-top: 40px;
}

.searchContainer h3 {
  font-size: 22px;
  color: #01348b;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.searchContainer > select {
  width: 200px;
  height: 40px;
  border-radius: 5px;
  color: #7c7c7c;
  font-family: "NanumSquareNeoLight";
  font-size: 16px;
  padding-left: 10px;
  cursor: pointer;
  border: none;
  margin: 0 30px;
}

.searchContainer > select:focus {
  outline: none;
}

.triangle {
  position: absolute;
  color: #7c7c7c;
  font-size: 20px;
  margin-left: -90px;
}

.search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search .searchBtn::placeholder {
  color: #7c7c7c;
  font-family: "NanumSquareNeoLight";
  display: flex;
  align-items: center;
  justify-content: center;
}

.search .searchIcon {
  color: #7c7c7c;
  font-size: 22px;
  cursor: pointer;
  margin-left: -40px;
}

.search > .searchBtn[type="text" i] {
  padding: 0;
}

.search > .searchBtn[type="text"] {
  background-color: #ffffff;
  border-radius: 5px;
  color: #7c7c7c;
  font-size: 16px;
  width: 400px;
  height: 40px;
  padding-left: 10px;
  border: none;
  display: flex;
  align-items: center;
}

.search .searchBtn:focus {
  outline: none;
}

.searchBoxShow {
  display: block;
}

.productSearch {
  width: 100%;
  display: flex;
  text-align: center;
  justify-content: center;
}

.productSearch > div {
  margin: 80px;
}

.productSearch > div > h4 {
  font-size: 20px;
  margin: 20px 0;
  color: #01348b;
}

.productSearch > div > p {
  font-size: 17px;
  margin: 5px 0;
}

.productSearch > div > .arrowIcon {
  color: #7c7c7c;
}

@media (max-width: 1750px) {
  .industryWrap {
    width: 900px;
  }
  .productSearch > div > h4 {
    font-size: 18px;
  }
  .productSearch > div > p {
    font-size: 15px;
  }
}
