.firstWrap {
  display: flex;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
}
.firstWrap .mainImg {
  width: 300px;
}
.imgWrap {
  width: 330px;
}
.subImgWrap {
  display: flex;
  justify-content: space-between;
}
.subImg {
  width: 100px;
  border: solid 1px #d8d8d8;
}

.eachInfo h4,
.eachInfo p {
  display: inline-block;
}

.eachInfo {
  box-sizing: border-box;
  border-bottom: solid 1px #d8d8d8;
  padding: 15px;
  display: flex;
  align-items: center;
}
.eachInfo h4 {
  width: 130px;
  border-right: 2px solid #01348b;
  align-items: center;
}

.eachInfo p {
  padding-left: 20px;
}
.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: end;
  border-top: 2px solid #01348b;
  padding-top: 10px;
}
.reserveBtn {
  width: 130px;
  padding: 10px 30px;
  background-color: white;
  margin-top: 20px;
  border: 1px solid #01348b;
  border-radius: 10px;
}

.secondWrap {
  box-sizing: border-box;
  width: 1200px;
  background-color: #f7f7f7;
  padding: 50px;
  margin-top: 50px;
  margin-bottom: 60px;
}

.titleWrap {
  display: flex;
}

.titleWrap h3 {
  margin-left: 20px;
  font-size: 16px;
}

.titleWrap img {
  width: 20px;
  height: 15px;
}

.secondWrap p {
  font-size: 14px;
  margin-top: 10px;
  margin-bottom: 50px;
}
.secondWrap ul {
  font-size: 14px;
  margin-top: 10px;
}

/* 반응형 */
@media (max-width: 1750px) {
  .container {
    width: 900px;
  }
  .firstWrap {
    width: 900px;
  }
  .infoWrap {
    width: 520px;
  }
  .eachInfo > p {
    width: 400px;
  }
  .secondWrap {
    width: 900px;
  }
}
