.graphWidth {
  display: flex;
  padding: 10px 0;
  font-size: 16px;
  text-align: center;
}

.graphWidth li:nth-child(1) {
  width: 10%;
}
.graphWidth li:nth-child(2) {
  width: 30%;
}

.graphWidth li:nth-child(3),
.graphWidth li:nth-child(4),
.graphWidth li:nth-child(5),
.graphWidth li:nth-child(6) {
  width: 15%;
}
