footer {
  width: 100%;
  background: #353535;
  height: 265px;
}

.footerTop {
  margin: 0 auto;
  width: 1200px;
  height: 70px;
  display: flex;
  justify-content: space-between;
}

.footerTop .menuMove {
  display: flex;
  width: 58%;
  justify-content: space-between;
  font-size: 15px;
  font-family: "NanumSquareNeoLight";
  align-items: center;
}

.footerTop .familySite {
  height: 70px;
  line-height: 70px;
  position: relative;
}

.footerTop .familySite .familyBtn {
  width: 220px;
  padding: 10px 10px;
  text-align: left;
  background-color: #686868;
  border: 1px solid #afafaf80;
  color: #ffffff;
  cursor: pointer;
  align-items: center;
  font-size: 15px;
}

.footerTop .familySite .familyBtn .faPlus {
  margin-left: 100px;
}

.familyContent {
  z-index: 999;
  position: absolute;
  width: 220px;
  top: 52px;
  border: 1px solid #afafaf80;
  background: #686868;
  box-sizing: border-box;
  display: none;
}

.familyContentShow {
  display: block;
}

.familyContent li {
  line-height: 30px;
  padding: 5px 0;
  z-index: 999;
  font-size: 14px;
  margin-left: 10px;
}

.familyContent li a {
  font-family: "NanumSquareNeoLight";
}

.familyContent li a:hover {
  color: #111111;
  font-weight: 600;
}

.footerBottom {
  border-top: 1px solid #83838367;
}

.footerBottom > div {
  width: 1200px;
  display: flex;
  margin: 40px auto;
  justify-content: space-between;
  color: #b2b2b2;
  align-items: center;
}

.footerBottom > div .address p {
  font-size: 14px;
  margin: 20px 0;
}
.footerBottom > div .address p.copyright {
  font-size: 11px;
  color: #777;
  margin: 0;
}

/* .footerBottom .callCenter { font-size: 14px; text-align: left; justify-content: left; } 
.footerBottom .callCenter p { margin: 10px 0; } */
.footerBottom .callCenter {
  font-size: 14px;
  border-collapse: collapse;
}
.footerBottom .callCenter td {
  padding: 4px 4px;
}

/* 반응형 */
@media (max-width: 1750px) {
  .footerTop {
    width: 980px;
  }
  .footerTop .menuMove {
    width: 58%;
    font-size: 14px;
  }
  .footerTop .familySite .familyBtn {
    font-size: 14px;
  }
  .footerTop .familySite .familyBtn .faPlus {
    margin-left: 110px;
  }
  .footerBottom > div {
    width: 980px;
  }
  .footerBottom .callCenter {
    font-size: 13px;
  }
  .footerBottom > div .address p.copyright {
    margin: 0;
  }
}
