@font-face {
  font-family: "NanumSquareNeoBold";
  src: url("../public/Font/NanumSquareNeoOTF-Bd.otf");
}

@font-face {
  font-family: "NanumSquareNeoRegular";
  src: url("../public/Font/NanumSquareNeoOTF-Rg.otf");
}

@font-face {
  font-family: "NanumSquareNeoLight";
  src: url("../public/Font/NanumSquareNeoOTF-Lt.otf");
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
button,
video {
  font-family: "NanumSquareNeoRegular";
  margin: 0;
  padding: 0;
  border: 0;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #ffffff;
}

body {
  overflow-y: scroll;
  overflow-x: hidden;
}
